/*
 * @file
 * This file is a custom file that loads all files. Each non-base layer
 * can be disabled.
 *
 * Do not name this file "STARTER.scss". This file should be named
 * neato_pod.scss. If you used the drush command to generate this subtheme,
 * it should be named for you.
 *
 * This application file (neato_pod.scss) is where all the partials are
 * imported.
 *
 * If you have changed the paths where bower components live, please update the mappings below.
 */

// Make sure the charset is set appropriately
@charset "UTF-8";

// Import normalize.css
@import "../../neato/bower_components/normalize.css/normalize.css";

// Import Bourbon.
@import "../../neato/bower_components/bourbon/app/assets/stylesheets/bourbon";

// Import Bitters. Includes Grid Setting Overrides, which must be imported before Neat
@import "base/base";

// Import Neat.
@import "../../neato/bower_components/neat/app/assets/stylesheets/neat";
@import "../../neato/bower_components/neat/app/assets/stylesheets/neat-helpers";

// Import Fonts
@include font-face("milonga", "../fonts/milonga/Milonga-Regular");
@include font-face("riffic", "../fonts/riffic/RifficFree-Bold");
@include font-face("maxwell", "../fonts/maxwell/MAXWELL REGULAR");
@include font-face("peignot", "../fonts/peignot/Peignot");

// Import layout
@import "layout/layout";

// Import Drupal element overrides
@import "common/breadcrumb";
@import "common/messages";
@import "common/tables";
@import "common/tasks";
@import "common/sidebars";
@import "common/pages";
@import "common/series";
@import "common/people";
@import "common/results";
@import "common/front";
@import "common/unit";
@import "common/episode";
@import "common/basicinfo";
@import "common/manage";
@import "common/open";

// Import your components here.
@import "components/hero";
@import "components/flexbox";
@import "components/footer";
@import "components/form-buttons";
@import "components/grid-list-toggle";
@import "components/header";
@import "components/sidr";


