/*
 *****************  Responsive sidebar to refine Solr search   **************************************************
 * This overwrites the basic layout in _layout.scss
 */

.context-results-page {

	&.one-sidebar.sidebar-first {

    main {
      aside {
        &#sidebar-first {
          @include span-columns(4); 
          @include media($tablet) {
              @include span-columns(0);
              display: none;
          }
        }
      }

      section#content {
          @include span-columns(8);
          @include media($tablet) {
              @include span-columns(6);
          }
        }
      }

  	}

	//Refine Search Buttons
	.featured-outer-two {

		#block-block-7 {
			// Do not display this except on $tablet and $mobile pages 
		  	display: none;
			float: right;

			#sidr-menu-episode, #sidr-menu-episode-keywords {
				background-color: $colorLinkHover;
				padding: 10px 15px;
				margin-left: 10px;

				color: $colorVLightGrey;
				@include media($mobile) {
					padding: 8px;
					font-size: .9em;
				}
				&:after {
					content: "≡";
					font-size: 2em;
					position: relative;
					top: 6px;
					padding-left: 10px;

					@include media($mobile) {
						top: 3px;
						font-size: 1.8em;
					}
				}
			}

		 

		  	@include media($tablet) {
		  		display: block;
		  		font-size: 1.4em;
		  	}

		  	@include media($mobile) {
		  		font-size: 1em;
		  	}

		}
	 

  	}
 }



 /*
 *****************  Solr Search Episode sidebar  **************************************************
 */


.context-results-page {
 
	// Hide "Keywords" block on Sidr search -- as it has its own block-7 when using mobile/tablet
    #sidr-id-block-block-12 {display: none;}
	#block-block-12 {
		h2 {
			background-color: $colorLinkHover;
			padding: 4px 10px 6px 10px;
			color: #fff;
			border-radius: 1px;
			margin: 0;
			font-variant: all-small-caps;
			font-size: 1.5em;
			font-weight: 400;
		}
		p {
			background-color: $colorVLightGrey;
			cursor: pointer;
			font-size: 1.1em;
			padding: 0 0 10px 10px;

			span {
				position: relative;
				top: 5px;
				margin-right: 10px;
				font-size: 2.2em;
			}
		}
		margin-bottom: 2em;
	}

	#sidebar-first {

		position: none;
		font-size: .9em;

		input {
			margin-bottom: $base-spacing / 2;
			background-color: #fff;
		}
	
		.contextual-links-region {position: static;}  // Some strange bug that affects only UNIT links block


		section {
			.item-list {
				padding: 4px;
				margin: 0 0 3em 0;
				background-color: $colorVLightGrey;
			}


			h2 {
				background-color: $colorLinkHover;
				padding: 4px 10px 6px 10px;
				color: #fff;
				border-radius: 1px;
				font-variant: all-small-caps;
				font-size: 1.5em;
				font-weight: 400;
			}


			.facetapi-facet-field-licence li {
				&:before {
					content: "cc";
					color: transparent; // Required if only to show image below
					background-image: url("../images/cc_logo_small.png");
					background-size: 18px;
					position: relative;
					top: 3px;
					//float: right;
					left: 5px;
					padding: 5px 10px;
					background-repeat: no-repeat;
				}
			}

			#sidr-menu-keywords {
				display: block;
				padding: 0 0 5px 3px;
				border-bottom: 1px solid $colorLightGrey;
				font-variant: small-caps;
				font-size: 1.2em;
				//text-align: center;
				//padding-left: 5px;
			}

		}

		.block-facetapi-units, .block-facetapi-keywords {

			margin: 0 0 2em 0;
				
			.item-list {
				max-height: 20em;
				overflow-y: scroll;

			}

			ul.facetapi-facetapi-links {
				//padding-right: -20px;

				li {
					border-bottom: 1px solid $base-border-color;
				}

			}

		}
	
	}

}


/*
 *****************  Sidebar for series, people, unit pages  **************************************************
 */


.context-series-page, .context-people-page, .context-unit-page {

	#sidebar-first {

		.block-fieldblock {
			background-color: $colorBeigeGrey;

			.field-item a {
				padding: 5px;
				display: block;
				&:hover {
					background-color: $colorLightGrey;
				}

				@include media($tablet) {
					font-size: .9em;
				}

				@include media($mobile) {
					font-size: .8em;
				}
			}

		}

		h2 {
			font-weight: normal;
			background-color: $colorLinkHover;
			padding: 5px;
			display: block;
			font-variant: small-caps;
			font-size: 1.4em;
			color: $colorVLightGrey;
			margin-bottom: 5px;
		}


	}
}

